<template>
    <div>
        <div class="page-header">
            <ul class="nav nav-pills">
                <li role="presentation">
                    <a href="#/feature/list"></a>
                </li>
                <li role="presentation" class="active">
                    <a href="#/feature/create_record"></a>
                </li>
            </ul>
        </div>

        <div class="row pad-top-10">
            <table class="table">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <template v-for="item in list">
                        <tr>
                            <td>
                                <strong>{{item.submit_time}}</strong>
                            </td>
                            <td>
                                <a href="javascript:;" @click="showDetail(item)">{{item.name}}</a>
                            </td>
                            <td>{{item.status_text}}</td>
                            <td>{{item.creator}}</td>
                            <td>{{item.describe}}</td>
                        </tr>
                    </template>
                </tbody>
            </table>
        </div>

        <modal modalId="recordDetailId" modalSize="modal-md">
            <template slot="header">
                <h4 class="modal-title" id="myModalLabel"></h4>
            </template>
            <ul class="list-group">
                <li class="list-group-item">
                    <strong>{{detail.name}}</strong>
                </li>
                <li class="list-group-item">
                    <strong>HADOOP</strong>
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{detail.source_dir}}
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{detail.db_type}}
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{detail.source_prefix}}
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{srcDataFormatText}}
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{detail.save_version_count}}
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{detail.data_size}}
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{detail.max_data_line}}
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{detail.check_cycle}}
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{detail.reload_cycle}}
                </li>
                <li class="list-group-item">
                    <strong></strong>
                    {{detail.expire_time}}
                </li>
            </ul>
            <template slot="footer">
              <!--<button type="button" class="btn btn-default" data-dismiss="modal" @click="modify()">-->
              <!--</button>-->
            </template>
        </modal>

    </div>
</template>

<script>
export default {
    name:'FeatureCreateRecord',
    data() {
        return {
            list: [],
            detail: {
                data_format_json: {
                    data_format: "",
                    storage_format: "",
                    delimiter: ""
                }
            }
        };
    },
    mounted() {
        this.getList();
    },
    computed: {
        srcDataFormatText: function() {
            return `${
                this.$config.common.dataStructConf[
                    this.detail.data_format_json.data_format
                ]
            }${
                this.$config.common.dataTypeConf[
                    this.detail.data_format_json.storage_format
                ]
            }${this.detail.data_format_json.delimiter}`;
        }
    },
    methods: {
        async getList() {
            let res = await this.$api.feature.getSubmitHistory();

            if (res.errcode != 0) {
                this.$set(this, "list", []);
                return;
            }

            this.$set(this, "list", res.data);
        },
        showDetail(detail) {
            this.detail = detail;
            this.detail.data_format_json = JSON.parse(this.detail.data_format);
            $("#recordDetailId").modal("show");
            localStorage.setItem('tempDetail', JSON.stringify(detail));
        },
        modify(){
            // $("#recordDetailId").modal("none");
          this.$router.push({
            path: `/feature/update/${this.detail.id}`,
          })
        }
    }
};
</script>
